import styled, { css } from "styled-components";
import { EngLoc } from "../../../assets/constants/localizations";
import Case from "./CaseDesktop";

import SberDesignSvg from "../../../assets/images/Designs/SberDesignSvg.svg";
import SKSkolkovoDesignSvg from "../../../assets/images/Designs/SKSkolkovoDesignSvg.svg";
import WallStreetDesignSvg from "../../../assets/images/Designs/WallStreetDesignSvg.svg";
import GlamourDesignSvg from "../../../assets/images/Designs/GlamourDesignSvg.svg";
import BKSDesignSvg from "../../../assets/images/Designs/BKSDesignSvg.svg";
import OKKODesignSvg from "../../../assets/images/Designs/OKKODesignSvg.svg";

const SAllCases = styled.div`
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 20px;
  overflow: hidden;
  transition: 0.5s ease-in-out;
  padding: 0 4%;

  @media (max-width: 1000px) {
    transform: translateY(-65px);
  }

  & > div {
    position: relative;
    max-width: calc(50% - 20px);

    @media (max-width: 1000px) {
      width: 100%;
      max-width: 100%;
    }
  }

  padding-top: 15.875rem;
  padding-bottom: 12.5rem;
  z-index: 2;

  background: radial-gradient(
      86.14% 110.41% at 0% -9.59%,
      #7400e8 0%,
      rgba(116, 0, 232, 0) 100%
    ),
    radial-gradient(
      38.96% 50.45% at 31.8% 17.2%,
      #ff7cda 0%,
      rgba(255, 124, 218, 0) 100%
    ),
    radial-gradient(
      59.26% 118.55% at 88.65% 113.3%,
      #00aee8 0%,
      rgba(0, 174, 232, 0) 100%
    ),
    radial-gradient(
      40.16% 70% at 52.73% 45.07%,
      #00aee8 0%,
      rgba(0, 174, 232, 0) 100%
    ),
    #000f67;

  & * {
    z-index: 100;
  }
`;

const Design = css`
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 2rem;
  z-index: 0;
  overflow: hidden;

  & > img {
    position: absolute;
    bottom: 0;
    right: 0;
    width: 100%;
  }
`;

const Background = styled.div`
  ${Design}
  ${({ design }) => design};
`;

const AllCases = (language) => {
  const Cases = [
    {
      title: "БКС Премьер",
      desc:
        language === "Ru"
          ? `БКС презентовал свои услуги и возможность инвестирования в продукты брокера для умножения своих средств`
          : EngLoc.partners.cases.cases_list[0],
      value1: "8 " + (language === "Ru" ? "Дней" : "Days"),
      value2: "316к",
      value3: "62к",
      design: (
        <Background
          design={css`
            background: linear-gradient(
              243.39deg,
              #1b6fff 4.1%,
              #6ba8ff 142.69%
            );
          `}
        >
          <img loading="lazy" src={BKSDesignSvg} alt="BKS" />
        </Background>
      ),
    },
    {
      title: "OKKO",
      desc:
        language === "Ru"
          ? `Реклама челленджа для киноманов от онлайн-кинотеатра Окко`
          : EngLoc.partners.cases.cases_list[1],
      value1: "11 " + (language === "Ru" ? "Дней" : "Days"),
      valueTitle2: language === "Ru" ? "Охват" : EngLoc.partners.cases.coverage,
      value2: "433к",
      design: (
        <Background
          design={css`
            background: linear-gradient(
              243.39deg,
              #210133 4.1%,
              #380a4b 142.69%
            );
          `}
        >
          <img loading="lazy" src={OKKODesignSvg} alt="OKKO" />
        </Background>
      ),
    },
    {
      title: "SK Сколково",
      desc:
        language === "Ru"
          ? `Рекламная кампания для Бизнес-школы Сколково в Телеграм -
      продвижение мастер-класса от Тома Роулинса. Как результат:
      sold-out`
          : EngLoc.partners.cases.cases_list[2],
      value1: "1 " + (language === "Ru" ? "Месяц" : "Month"),
      value2: "316к",
      value3: "79к",
      design: (
        <Background
          design={css`
            background: linear-gradient(
              243.39deg,
              #593ff5 4.1%,
              #9a7ffc 142.69%
            );
            & > img {
              bottom: -1.875rem;
            }
          `}
        >
          <img loading="lazy" src={SKSkolkovoDesignSvg} alt="SKSkolkovo" />
        </Background>
      ),
    },
    {
      title: "Glamour",
      desc:
        language === "Ru"
          ? `Размещения в Телеграм для Glamour - тестирование нового инструмента для крупного глянцевого издания`
          : EngLoc.partners.cases.cases_list[3],
      value1: "1 " + (language === "Ru" ? "Месяц" : "Month"),
      value2: "5к",
      value3: "183к",
      design: (
        <Background
          design={css`
            background: linear-gradient(
              243.39deg,
              #e71c8d 4.1%,
              #ffb6e0 142.69%
            );
          `}
        >
          <img loading="lazy" src={GlamourDesignSvg} alt="Glamour" />
        </Background>
      ),
    },
    {
      title: "Wall Street English",
      desc:
        language === "Ru"
          ? `Реклама школы английского языка на жителей Москвы с помощью посевов на всех площадках SocialJet`
          : EngLoc.partners.cases.cases_list[4],
      value1: "5 " + (language === "Ru" ? "Месяцев" : "Months"),
      value2: "2к",
      value3: "4,7 " + (language === "Ru" ? "Млн" : "Million"),
      design: (
        <Background
          design={css`
            background: linear-gradient(
              243.39deg,
              #e03d46 4.1%,
              #023260 86.87%
            );
          `}
        >
          <img loading="lazy" src={WallStreetDesignSvg} alt="WallStreet" />
        </Background>
      ),
    },
    {
      title: "Сбербанк",
      desc:
        language === "Ru"
          ? `Сбербанк анонсирует запуск проекта инвестирования 500 лучших стартапов`
          : EngLoc.partners.cases.cases_list[5],
      value1: "12 " + (language === "Ru" ? "Дней" : "Days"),
      value2: "898к",
      value3: "216к",
      design: (
        <Background
          design={css`
            background: linear-gradient(
              243.39deg,
              #17b82e 4.1%,
              #8fff36 142.69%
            );
          `}
        >
          <img loading="lazy" src={SberDesignSvg} alt="Sber" />
        </Background>
      ),
    },
  ];

  return (
    <SAllCases>
      <Case
        active={true}
        language={language}
        title={Cases[0].title}
        desc={Cases[0].desc}
        value1={Cases[0].value1}
        value2={Cases[0].value2}
        value3={Cases[0].value3}
        valueTitle1={Cases[0].valueTitle1}
        valueTitle2={Cases[0].valueTitle2}
        valueTitle3={Cases[0].valueTitle3}
        design={Cases[0].design}
      />
      <Case
        active={true}
        language={language}
        title={Cases[1].title}
        desc={Cases[1].desc}
        value1={Cases[1].value1}
        value2={Cases[1].value2}
        value3={Cases[1].value3}
        valueTitle1={Cases[1].valueTitle1}
        valueTitle2={Cases[1].valueTitle2}
        valueTitle3={Cases[1].valueTitle3}
        design={Cases[1].design}
      />
      <Case
        active={true}
        language={language}
        title={Cases[2].title}
        desc={Cases[2].desc}
        value1={Cases[2].value1}
        value2={Cases[2].value2}
        value3={Cases[2].value3}
        valueTitle1={Cases[2].valueTitle1}
        valueTitle2={Cases[2].valueTitle2}
        valueTitle3={Cases[2].valueTitle3}
        design={Cases[2].design}
      />
      <Case
        active={true}
        language={language}
        title={Cases[3].title}
        desc={Cases[3].desc}
        value1={Cases[3].value1}
        value2={Cases[3].value2}
        value3={Cases[3].value3}
        valueTitle1={Cases[3].valueTitle1}
        valueTitle2={Cases[3].valueTitle2}
        valueTitle3={Cases[3].valueTitle3}
        design={Cases[3].design}
      />
      <Case
        active={true}
        language={language}
        title={Cases[4].title}
        desc={Cases[4].desc}
        value1={Cases[4].value1}
        value2={Cases[4].value2}
        value3={Cases[4].value3}
        valueTitle1={Cases[4].valueTitle1}
        valueTitle2={Cases[4].valueTitle2}
        valueTitle3={Cases[4].valueTitle3}
        design={Cases[4].design}
      />
      <Case
        active={true}
        language={language}
        title={Cases[5].title}
        desc={Cases[5].desc}
        value1={Cases[5].value1}
        value2={Cases[5].value2}
        value3={Cases[5].value3}
        valueTitle1={Cases[5].valueTitle1}
        valueTitle2={Cases[5].valueTitle2}
        valueTitle3={Cases[5].valueTitle3}
        design={Cases[5].design}
      />
    </SAllCases>
  );
};

export default AllCases;
